body {
  text-align: center !important;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: white;
}


.responsiveContainer {
	margin-top : 15%;
}

.sidebarIcons{
	color:white;
	font-size: 2rem;
	margin: 0 1rem 0 0;
	transition: all .2s ease-in-out;
}

.sidebarIcons:hover{
	color:#f52549;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.ColorSection{
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}



