.footerIcons{
	color:white;
	font-size: 3rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.footerIcons:hover{
	color: rgb(249, 248, 113);
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.sampleContainer {
    display: grid;
    overflow-y: hidden;
    text-align: left;
    border-bottom: 1px solid lightgray;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.sampleTextStyle {
  text-decoration: none;
  padding-left: 1rem;
}

.sampleImgContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    overflow-x: auto;
    overflow-y: hidden;
    padding-bottom: 1rem;
}

.sampleImgStyle {
  margin: 1rem;
  border-radius : 2rem;
  transition: all .2s ease-in-out;
  width: 160;
  height: auto;
}

.sampleImgStyle:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media only screen and (max-width: 600px) {
  .sampleImgStyle {
    margin: 0.5rem;
  }

}
